import {Load} from "../../../../../../common/react/load";
import {createArray} from "../../../../../../common/utils/collections";
import {cs} from "../../../../../../common/react/chain-services";
import * as React from "react";
import {LsState} from "../../../../../../common/react/ls-state";
import "../common/main.scss";
import {Layout} from "../../common/layout/layout";
import {genNumbers} from "../common/gen-numbers";
import {genInt} from "../common/generators/common/gen-int";
import {formatEquation} from "../common/format-equation";
import {bindInputNumber} from "../../../../../../common/react/bind-input-number";
import {scope} from "../../../../../../common/react/scope";

export const MathPlusMinus110 = ({}) => cs(
    ({}, next) => Layout({next}),
    ["config", (_, next) => LsState({
        key: "math-minus.config",
        // defaultValue: {
        //     max: 110,
        //     numCount: 3,
        //
        // },
        next,
    })],

    ["result", ({config}, next) => Load({
        _key: JSON.stringify(config.value),
        fetch: () => generate({
            max: 110,
            numCount: 3,
            eqCount: 10,
            ...config.value,
        }),
        next,
    })],
    ({result, config}) => result && (
        <div className="math-main-1">
            <div className="">
                Cộng trừ dưới 110
            </div>
            <div className="">
                {result.map((eq, i) => (
                    <div key={i}>
                        {i+1}. {formatEquation(eq)} = ?
                    </div>
                ))}
            </div>

            <div className="config">
                <div className="">
                    Max:
                    <input {...{
                        ...bindInputNumber(scope(config, ["max"]))
                    }} />
                </div>
                <div className="">
                    Num Count:
                    <input {...{
                        ...bindInputNumber(scope(config, ["numCount"]))
                    }} />
                </div>
                <div className="">
                    Equation Count:
                    <input {...{
                        ...bindInputNumber(scope(config, ["eqCount"]))
                    }} />
                </div>
            </div>
        </div>
    )
);

const generate = ({max, numCount}) => {
    return createArray(10).map(() => genNumbers([
        // () => genInt(0, max),
        ...createArray(numCount).map(() =>
            (prev) => genInt(0, max, (n) => prev.findIndex((n1) => n1 === n) === -1),
        ),
        // (prev) => genInt(0, max, (n) => prev.findIndex((n1) => n1 === n) === -1),
    ]));
};
